@import "../../styles/globals.scss";
@import "../../styles/responsive.scss";

.experienceItemContainer {

  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  display: flex;
  flex-flow: row wrap;
  align-items: center;
  justify-content: flex-end;
  border-top: solid 1px #7c7c7c;
  padding: 10px;
  margin: 5px;
  background-color: $darkGrayish;

  .expItemRole {
    color: #ff9fcf;
    font-size: .9rem;
    font-weight: 800;
    padding-bottom: 5px;
  }

  .expItemStack {
    padding: 2px;
    font-size: 0.8rem;
    font-weight: 600;
    color: rgb(255, 254, 200);
  }

  .expItemLabel {
    padding-bottom: 2px;
    font-weight: 200;
    font-size: small;
  }

  .expItemDate {
    align-self: flex-end;
    bottom: 20px;
    right: 60px;
    font-style: italic;
    font-size: 0.8rem;
    line-height: 1.1;
    padding: 2px;
    padding-left: 5px;
  }
}